$bg-black: rgba(255, 255, 255, 0.1);

.breedsPage {
  height: 770px;
  display: grid;

  grid-template-rows: 60px 1fr;
  background-color: #fff;
  border-radius: 20px;
  padding: 20px;
  box-sizing: border-box;
}

.wrapper {
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  display: grid;
  grid-template-rows: 1fr;
}

.breedsList {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 20px;
  height: 100%;
  overflow-y: scroll;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
  //  overflow-x: scroll;
  //  overflow-y: hidden;
  &::-webkit-scrollbar {
    width: 0 !important;
    height: 0;
    display: none !important;
    background: transparent;
  }

  .bottomWrapper {
    margin-top: 40px;
    flex: 1 1 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.grid__layout {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, 200px);
  // grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-template-rows: 140px repeat(auto-fit, 140px);
  // grid-template-rows: 140px repeat(auto-fill,140px);
  .grid__item {
    position: relative;
    padding: 0 0 140px 0;
    border-radius: 20px;
    overflow: hidden;
    &:nth-child(1),
    &:nth-child(10n + 1),
    &:nth-child(8),
    &:nth-child(10n + 8) {
      grid-row: span 2;
      padding: 0 0 300px 0;
    }
    &:nth-child(4),
    &:nth-child(10n + 4),
    &:nth-child(9),
    &:nth-child(10n + 9) {
      grid-row: span 2;
      grid-column: span 2;
      padding: 0 0 300px 0;
    }
    img {
      position: absolute;
      top: 0%;
      left: 0%;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    div {
      position: absolute;
      height: 100%;
      width: 100%;
      left: 0;
      top: 0;
      background: rgba(255, 134, 142, 0.6);
      display: inline-flex;
      justify-content: center;
      align-items: flex-end;
      padding-bottom: 10px;
      opacity: 0;
      transition: opacity 0.5s;
      span {
        display: inline-flex;
        width: 180px;
        height: 34px;
        justify-content: center;
        align-items: center;
        background: #ffffff;
        border-radius: 10px;
        color: #ff868e;
        font-family: "Jost";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
      }
    }

    &:hover {
      div {
        opacity: 1;
        cursor: pointer;
      }
    }
  }

  .grid__item_gallery {
    div {
      align-items: center;
      padding: 0;
      button {
        width: 40px;
        height: 40px;
        font-size: 0;
        border-radius: 10px;
      }
      .add {
        background: #ffffff url(../../../../assets/icons/search-form/favourities.svg) no-repeat center center;
        background-size: 20px 17px;
      }

      .remove {
        background: #ffffff url(../../../../assets/icons/voting-page/favourited.svg) no-repeat center center;
        background-size: 20px 17px;
      }

      .remove_from_likes {
        background: #ffffff url(../../../../assets/icons/likes-page/like.svg) no-repeat center center;
        background-size: 20px 17px;
      }

      .remove_from_dislikes {
        background: #ffffff url(../../../../assets/icons/dislikes-page/dislike.svg) no-repeat center center;
        background-size: 20px 17px;
      }
    }
  }

  @media (max-width: 740px) {
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr;
    // grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-template-rows: repeat(auto-fit, 300px);
    .grid__item {
      position: relative;
      padding: 0 0 300px 0;
      border-radius: 20px;
      overflow: hidden;
      &:nth-child(1),
      &:nth-child(10n + 1),
      &:nth-child(8),
      &:nth-child(10n + 8) {
        grid-row: span 1;
        padding: 0 0 300px 0;
      }
      &:nth-child(4),
      &:nth-child(10n + 4),
      &:nth-child(9),
      &:nth-child(10n + 9) {
        grid-row: span 1;
        grid-column: span 1;
        padding: 0 0 300px 0;
      }
      img {
        position: absolute;
        top: 0%;
        left: 0%;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      div {
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
        background: rgba(255, 134, 142, 0.6);
        display: inline-flex;
        justify-content: center;
        align-items: flex-end;
        padding-bottom: 10px;
        opacity: 0;
        transition: opacity 0.5s;
        span {
          display: inline-flex;
          width: 180px;
          height: 34px;
          justify-content: center;
          align-items: center;
          background: #ffffff;
          border-radius: 10px;
          color: #ff868e;
          font-family: "Jost";
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
        }
      }

      &:hover {
        div {
          opacity: 1;
          cursor: pointer;
        }
      }
    }

    .grid__item_gallery {
      div {
        align-items: center;
        padding: 0;
        button {
          width: 40px;
          height: 40px;
          font-size: 0;
          border-radius: 10px;
        }
        .add {
          background: #ffffff url(../../../../assets/icons/search-form/favourities.svg) no-repeat center center;
          background-size: 20px 17px;
        }

        .remove {
          background: #ffffff url(../../../../assets/icons/voting-page/favourited.svg) no-repeat center center;
          background-size: 20px 17px;
        }

        .remove_from_likes {
          background: #ffffff url(../../../../assets/icons/likes-page/like.svg) no-repeat center center;
          background-size: 20px 17px;
        }

        .remove_from_dislikes {
          background: #ffffff url(../../../../assets/icons/dislikes-page/dislike.svg) no-repeat center center;
          background-size: 20px 17px;
        }
      }
    }
  }
  @media (max-width: 550px) {
    grid-template-rows: repeat(auto-fit, 206.5px);
    gap: 10px;
    .grid__item {
      padding: 0 0 206.5px 0;
      &:nth-child(1),
      &:nth-child(10n + 1),
      &:nth-child(8),
      &:nth-child(10n + 8) {
        grid-row: span 1;
        padding: 0 0 206.5px 0;
      }
      &:nth-child(4),
      &:nth-child(10n + 4),
      &:nth-child(9),
      &:nth-child(10n + 9) {
        grid-row: span 1;
        grid-column: span 1;
        padding: 0 0 206.5px 0;
      }
    }
  }
}
//BLACK THEME LIST---------------------------------------------------------------------------------------------
.grid__layout.black {
  .grid__item {
    span {
      background-color: #282828;
    }
  }
  .grid__item_gallery {
    div {
      .add,
      .remove,
      .remove_from_likes,
      .remove_from_dislikes {
        background-color: #282828;
      }
    }
  }
}

//-------------------------------------------------------------------------------------------------------------

.breedsFilterForm {
  // padding-bottom: 20px;

  form {
    // display: flex;
    display: grid;
    // justify-content: space-between;
    grid-auto-flow: column;
    gap: 10px;
  }

  @media (max-width: 541px) {
    width: 100%;
    margin-top: 10px;
    form {
      // width: 100%;
      margin-top: 0;
      grid-template-rows: 40px 40px;
      grid-template-columns: 1fr 40px 40px;
    }
  }
}

.element {
  display: inline-block;
  height: 40px;
  border: none;
  border-radius: 10px;
  background: #f8f8f7;
  color: #8c8c8c;

  font-size: 16px;
  font-family: "Jost";
  font-style: normal;
  font-weight: 400;
  // margin-right: 10px;
}

.btn {
  width: 40px;
  font-size: 0;
}

.btn_back {
  background: #fbe0dc url(../../../../assets/icons/breeds-page/back-btn.svg) no-repeat center center;
}

.btn_AtoZ {
  background: #f8f8f7 url(../../../../assets/icons/breeds-page/btn_AtoZ.svg) no-repeat center center;
  &:active {
    background: #f8f8f7 url(../../../../assets/icons/breeds-page/btn_AtoZ_pink.svg) no-repeat center center;
  }
}

.btn_ZtoA {
  background: #f8f8f7 url(../../../../assets/icons/breeds-page/btn_ZtoA.svg) no-repeat center center;
  &:active {
    background: #f8f8f7 url(../../../../assets/icons/breeds-page/btn_ZtoA_pink.svg) no-repeat center center;
  }
}

.btn_AtoZ,
.btn_ZtoA {
  cursor: pointer;
  &:active {
    outline: 2px solid #fbe0dc;
  }
}

.breedsFilter,
.limitFilter {
  padding: 0 10px;
  width: 100%;
  -moz-appearance: none;
  -webkit-appearance: none;
  box-sizing: border-box;
  background: #f8f8f7 url(../../../../assets/icons/breeds-page/select_arrow.svg) no-repeat right 10px center;
  cursor: pointer;
  &:focus {
    outline: 2px solid #fbe0dc;
  }
}

.breedsFilter {
  width: 226px;
  @media (max-width: 541px) {
    width: 100%;
    grid-column: span 3;
  }
}

.limitFilter {
  width: 101px;
  @media (max-width: 541px) {
    width: 100%;
  }
}

.pageName {
  padding: 0 30px;
  background: #ff868e;

  font-family: "Jost";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;

  text-align: center;
  letter-spacing: 2px;

  color: #ffffff;

  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.paginator {
  padding-top: 20px;
  display: flex;
  justify-content: center;
  div {
    display: grid;
    grid-template-areas: "prev space next";
    grid-template-columns: 122px 40px 122px;
    width: 284px;
  }
}

.paginator.black {
  .btn_prev,
  .btn_next {
    background: rgba(255, 255, 255, 0.05);
    &:hover {
      background: rgba(255, 134, 142, 0.2);
    }
    &:active {
      background-color: #ff868e;
    }
  }
}

.btn_prev,
.btn_next {
  width: 122px;

  font-family: "Jost";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;

  letter-spacing: 2px;

  color: #8c8c8c;

  position: relative;

  background: #f8f8f7;

  &::after {
    content: "";
    position: absolute;
    background: url(../../../../assets/icons/breeds-page/prevNext_btn_gray.svg) no-repeat center center;
    width: 7px;
    height: 100%;

    top: 0;
  }
  &:hover {
    cursor: pointer;
    background: #fbe0dc;
    color: #ff868e;
    &::after {
      background: url(../../../../assets/icons/breeds-page/prevNext_btn_pink.svg) no-repeat center center;
    }
  }
  &:active {
    background: #ff868e;
    color: #fff;
    &::after {
      background: url(../../../../assets/icons/breeds-page/prevNext_btn_white.svg) no-repeat center center;
    }
  }
}

.btn_prev {
  grid-area: prev;
  text-align: right;
  padding-right: 34px;
  &::after {
    left: 34px;
  }
}

.btn_next {
  grid-area: next;
  text-align: left;
  padding-left: 34px;
  &::after {
    right: 34px;
    transform: rotate(180deg);
  }
}

//BLACK THEME FILTER FORM---------------------------------------------------------------------------------------------
.breedsFilterForm.black {
  .element {
    background-color: rgba(255, 255, 255, 0.05);
    &:active {
      outline: 2px solid rgba(255, 134, 142, 0.2);
    }
  }

  .breedsFilter,
  .limitFilter {
    &:focus {
      outline: 2px solid rgba(255, 134, 142, 0.2);
    }
  }
}
